	methods: {
		// 减少商品数量
		reduce: function() {
			let that = this;
			let num = that.num;
			if (num > 1) {
				that.num = num -1;
			}
		},
		// 增加商品数量
		plus: function() {
			let that = this;
			let num = that.num;
			if (num < 99) {
				that.num = num + 1;
			}
		},
		// 点击开始兑换商品
		exchange: function(e) {
			//console.log(e);
			let that = this;
			let countScore = that.wares.score * that.num;
			that.$showLoading();   //显示遮罩
			that.$request.postToken("/users/customer/checkScore.do", {
				score: countScore
			}).then((res) => {
				//console.log(res);
				if (res.data.status === 0) {
					let waresid = that.wares.waresId;
					uni.navigateTo({
						url: '../exchange/exchange?waresId=' + waresid + '&num=' + that.num
					});
				} else {
					that.$util.showToast(res.data.results, 'none', 3000);
				}
			}).catch((err) => {
				console.log(err);
				that.$util.showToast(err.errMsg, 'none', 4000);
			}).finally(() => {
				that.$hideLoading();
			})
		},
